import React, {useState, useEffect} from 'react';

import bridge from '@vkontakte/vk-bridge';
import {View, ScreenSpinner, AdaptivityProvider, AppRoot} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Curs2 from "./panels/Curs2";
import Curs3 from "./panels/Curs3";
import Curs4 from "./panels/Curs4";
import Curs5 from "./panels/Curs5";
import Buttons from "./panels/components/Buttons";
import UserText from "./panels/components/UserText";


const App = () => {


    const [activePanel, setActivePanel] = useState('home');
    const [fetchedUser, setUser] = useState(null);
    const [popout, setPopout] = useState(<ScreenSpinner size='large'/>);


    useEffect(() => {
        bridge.subscribe(({detail: {type, data}}) => {
            if (type === 'VKWebAppUpdateConfig') {
                const schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
                document.body.attributes.setNamedItem(schemeAttribute);
            }
        });

        async function fetchData() {
            const user = await bridge.send('VKWebAppGetUserInfo');
            setUser(user);
            setPopout(null);
        }

        fetchData();
    }, []);


    const go = e => {
        setActivePanel(e.currentTarget.dataset.to);
    };

    return (
        <AdaptivityProvider>
            <AppRoot>
                <View activePanel={activePanel} popout={popout}>
                    <Home id='home' fetchedUser={fetchedUser} go={go}/>
                    <Persik id='persik' go={go}/>
                    <Curs2 id='curs2' go={go}/>
                    <Curs3 id='curs3' go={go}/>
                    <Curs4 id='curs4' go={go}/>
                    <Curs5 id='curs5' go={go}/>
                    <Buttons id='buttons' go={go}/>
                    <UserText id='usertext' go={go} fetchedUser={fetchedUser}/>
                </View>
            </AppRoot>
        </AdaptivityProvider>
    );

}

export default App;
