import React from 'react';
import './Persik.css'
import './blok.css'
import { Button, Div, Group, Panel, PanelHeader, PanelHeaderBack} from '@vkontakte/vkui';
import {Icon28ChevronRightOutline, Icon28UserOutline} from "@vkontakte/icons";




const Curs5 =  ({id,go})  => (
    <Panel id={id}>
        <PanelHeader
            left={<PanelHeaderBack onClick={go} data-to="home"/>}
        >
            5 Курс
        </PanelHeader>
        <Group className="Persiki" mode={"card"} >
            <Div>
                <Button size="l" mode="secondary" expandable before={<Icon28UserOutline />} onClick={go} data-to="home" className="buton" >Профиль</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />} onClick={go} data-to="persik" className="buton" >1 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />} onClick={go} data-to="curs2" className="buton" >2 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />} onClick={go} data-to="curs3" className="buton" >3 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />} onClick={go} data-to="curs4" className="buton" >4 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />} onClick={go} data-to="curs5" className="buton" >5 Курс</Button>
            </Div>
        </Group>
        <img  src={process.env.PUBLIC_URL + "/5curs.png"} className="img"/>


    </Panel>
);


export default Curs5;
