import React from 'react';

import {Div, Group, Header, Panel, Text} from "@vkontakte/vkui";

const UserText = ({id}) => {
    return (
        <Panel id={id}>

            <Group className="cart" mode={"card"} header={<Header mode="secondary">Информация о вузе(Новости)</Header>}>
                <Div>
                    <Text>
                        Подготовка конкурентоспособных специалистов высокого уровня компетентности с учетом требований
                        модернизации экономики работодателя, способных работать в экономических системах, основанных на
                        знаниях, активно влиять на инновационное социально- экономическое, духовное развитие Ивановского
                        края, России.
                    </Text>
                </Div>
            </Group>

            <Group className="raz" mode={"card"} header={<Header mode="secondary">Версия 2.1.1</Header>}>
                <Div>
                    <Text>
                        • Добавлен новый дизайн
                    </Text>
                    <Text>
                        • Исправлены ошибки в совместимости
                    </Text>
                    <Text>
                        • Исправлены мелкие недочёты
                    </Text>
                </Div>
            </Group>
        </Panel>
    );
};

export default UserText;